<template>
<div class="orderlist">
	<div class="content-title">
      <!--  -->

      <div class="titlename">
      	<span></span>
      </div>
      <div class="titellink">
        <Select v-model="orderquery.payType" slot="prepend" style="width:30%;" placeholder="">
          <Option value=""></Option>
          <Option value="2"></Option>
          <Option value="5"></Option>
          <Option value="4"></Option>
        </Select>
        <Input v-model="orderquery.remarks" placeholder=""  style="width:30%"></Input>
        <Button type="primary" icon="ios-search" @click = "getOrderList(1)" ></Button>
      </div>
 	</div>
 	<div class="content">
  <Table border :columns="orderlistColumns" :data="orderlistData" class="orderlistable"></Table>
    <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="pager.pages" :current="pager.current" @on-change="changePage($event)"></Page>
        </div>
    </div>
 	</div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Orders',
  data () {
    return {
      orderquery:{
        payType: '',
        remarks: ''
      },
      orderlistColumns: [
        {
          title: '',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: '',
          key: 'payNo'
        },
        {
          title: '',
          key: 'name'
        },
        {
          title:"",
          key:'headImg',
          render (row) {
            return '<img :src="row.account.headImg" style="width:40px;height:40px;" v-if="row.account&&row.account.headImg"/>'
          }
        },
        {
          title: '',
          key: 'discount',
          render (row) {
            return '<span>{{row.discount}}</span>'
          }
        },
        {
          title: '',
          key: 'payAmount'
        },
        {
          title: '',
          key: 'payPoints'
        },
        {
          title: '',
          key: 'payStatus',
          render (row) {
            return row.payStatus==1?'<Icon type="checkmark-round"></Icon>':'<Icon type="alert" color="red" title=""></Icon>'
          }
        },
        {
          title: '',
          key: 'payDate'
        },
        {
          title: '',
          key: 'remarks'
        }
      ],
      orderlistData: [],
      pager: {
        pages: 1,
        current: 1
      }
    }
  },
  created () {
    this.getOrderList(1)
  },
  methods: {
    getOrderList (pageNo) {
      this.http.get(this.$store.state.prefix + '/pay/page/' + (pageNo || 1) + '?payType=' +this.orderquery.payType+'&remarks='+this.orderquery.remarks).then(res => {
        if (res.error === false) {
          this.pager = res.result
          this.orderlistData = res.result.records
          this.orderlistData.forEach(item=>{
            item.payDate = this.util.getFormatDate(item.payDate);
            item.name = item.account?(item.account.realName == ''?item.account.nickName:item.account.realName):'';
            item.headImg = item.account.headImg;
          })
        }
      })
    },
    changePage (e) {
      this.getOrderList(e)
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
</style>



